"use client";

import { useState } from "react";
import { AgenticComposer } from "@/components/AgenticComposer";
import { ComponentPreview } from "@/components/ComponentPreview";
import { FileStructureViewer } from "@/components/FileStructureViewer";
import { GenerativeComponent } from "@/components/GenerativeComponent";
import { InteractableComponent } from "@/components/InteractableComponent";
import { PageCanvas } from "@/components/PageCanvas";
import { FlowEditor } from "@/components/FlowEditor";
import { AuthWidget } from "@/components/AuthWidget";
import { APITest } from "@/components/APITest";
import { DeployStatus } from "@/components/DeployStatus";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import TestCSS from "./test-css";

export default function Home() {
  const [activeTab, setActiveTab] = useState("composer");

  return (
    <main className="container mx-auto px-4 py-8">
      <div className="max-w-7xl mx-auto">
        {/* CSS Test - Remove this after testing */}
        <div className="mb-8">
          <TestCSS />
        </div>

        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gradient mb-2">
            Tambo AI Agentic Composer
          </h1>
          <p className="text-lg text-muted-foreground">
            Transform your ideas into working React applications through AI
            conversation
          </p>
        </div>

        {/* Navigation Tabs */}
        <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="composer">Composer</TabsTrigger>
            <TabsTrigger value="components">Components</TabsTrigger>
            <TabsTrigger value="canvas">Canvas</TabsTrigger>
            <TabsTrigger value="tools">Tools</TabsTrigger>
          </TabsList>

          {/* Composer Tab */}
          <TabsContent value="composer">
            <Card className="tambo-card">
              <CardHeader>
                <CardTitle>AI Product Composer</CardTitle>
                <CardDescription>
                  Describe your application requirements and let Tambo AI build
                  it for you
                </CardDescription>
              </CardHeader>
              <CardContent>
                <AgenticComposer />
              </CardContent>
            </Card>
          </TabsContent>

          {/* Components Tab */}
          <TabsContent value="components">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <Card className="tambo-card">
                <CardHeader>
                  <CardTitle>Generative Components</CardTitle>
                  <CardDescription>
                    AI-generated React components from natural language
                    descriptions
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <GenerativeComponent />
                </CardContent>
              </Card>

              <Card className="tambo-card">
                <CardHeader>
                  <CardTitle>Interactable Components</CardTitle>
                  <CardDescription>
                    Components that can be modified and enhanced through Tambo
                    AI
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <InteractableComponent />
                </CardContent>
              </Card>

              <Card className="tambo-card col-span-1 md:col-span-2">
                <CardHeader>
                  <CardTitle>Component Preview</CardTitle>
                  <CardDescription>
                    Live preview of generated components
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <ComponentPreview />
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Canvas Tab */}
          <TabsContent value="canvas">
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
              <Card className="tambo-card lg:col-span-2">
                <CardHeader>
                  <CardTitle>Page Canvas</CardTitle>
                  <CardDescription>
                    Visual interface builder for your application
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <PageCanvas />
                </CardContent>
              </Card>

              <Card className="tambo-card">
                <CardHeader>
                  <CardTitle>File Structure</CardTitle>
                  <CardDescription>
                    Project file structure and management
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <FileStructureViewer />
                </CardContent>
              </Card>

              <Card className="tambo-card lg:col-span-3">
                <CardHeader>
                  <CardTitle>Flow Editor</CardTitle>
                  <CardDescription>
                    Visual workflow and state management editor
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <FlowEditor />
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Tools Tab */}
          <TabsContent value="tools">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              <Card className="tambo-card">
                <CardHeader>
                  <CardTitle>Auth Widget</CardTitle>
                  <CardDescription>
                    Authentication and user management
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <AuthWidget />
                </CardContent>
              </Card>

              <Card className="tambo-card">
                <CardHeader>
                  <CardTitle>API Test</CardTitle>
                  <CardDescription>
                    Test and validate your API endpoints
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <APITest />
                </CardContent>
              </Card>

              <Card className="tambo-card">
                <CardHeader>
                  <CardTitle>Deploy Status</CardTitle>
                  <CardDescription>
                    Monitor deployment status and health
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <DeployStatus />
                </CardContent>
              </Card>
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </main>
  );
}
